<template>
	<div class="_full_router fund-manage-con">
			<custHeader :title="title"></custHeader>
			<div class="fund-manage-contain">
				<div v-show="flowId==1">
					<!-- <mt-field label="*证件类型" placeholder="请选择证件类型" v-model="idType.name" v-lfoucs="selectTypeVisibleShowAcct" v-remoteoptions="dicts.acctType" ></mt-field> -->
					<mt-field label="*客户姓名" placeholder="请输入客户姓名" v-model="investorSname" v-validtype="rules.investorSname" :state="rules.investorSname.state" ></mt-field>
					<mt-field label="*证件类型" placeholder="请选择证件类型" v-model="idType.name" v-lfoucs="selectTypeVisibleShowAcct" v-remoteoptions="dicts.idType" ></mt-field>
					<mt-field label="*证件号码" placeholder="请输入证件号码" v-model="idCode" v-validtype="rules.idCode" :state="rules.idCode.state" ></mt-field>
					<!-- <mt-field label="*证件名称" placeholder="请输入证件名称" v-model="idName" v-validtype="rules.idName" :state="rules.idName.state"></mt-field> -->
					<mt-field label="*证件有效期" placeholder="请输入证件有效期" v-model="certValidDate" v-validtype="rules.certValidDate" :state="rules.certValidDate.state" ></mt-field>
					<mt-field label="*发证机关" placeholder="请输入发证机关" v-model="idIssAgcy"v-validtype="rules.idIssAgcy" :state="rules.idIssAgcy.state" ></mt-field>
					<!-- <img :src="zjz" class="zjz" v-show="zjzshow">
					<input type="file" id="uploadPic" accept="image"  @change="upload" />
					<mt-button   type="primary" size="large"  @click="showSheet">上传证件照</mt-button> -->
					<mt-button  type="primary" size="large"  @click="showFlow(2)">下一步</mt-button>
				</div>
				<div v-show="flowId==2">
					<mt-header :title="title" class="cust-header1" style="background:#ef4f4f" ><mt-button icon="back" @click="showFlow(1)" slot="left">返回</mt-button></mt-header>
					<mt-field label="*投资者国籍" placeholder="请输入投资者国籍" v-model="nationality.name" v-lfoucs="selectTypeVisibleShow2" v-remoteoptions="dicts.countryCode"></mt-field>
					<mt-field label="*省/直辖市" placeholder="请选择省/直辖市" v-model="province.name" v-lfoucs="selectTypeVisibleShow3" v-areas="areas.province" ></mt-field>
					<mt-field label="*市" placeholder="请输入市" v-model="city.name" v-lfoucs="selectTypeVisibleShow4" v-areas="areas.city" ></mt-field>
					<mt-field label="*县/区" placeholder="请输入县/区" v-model="area.name" v-lfoucs="selectTypeVisibleShow5" v-areas="areas.area"></mt-field>
					<mt-field label="*邮政编码" placeholder="请输入邮政编码" v-model="postcode" v-validtype="rules.postcode" :state="rules.postcode.state" ></mt-field>
					<mt-field label="*通信地址" placeholder="请输入通信地址" v-model="address" v-validtype="rules.address" :state="rules.address.state" ></mt-field>
					<mt-field label="*紧急联系人1姓名" class="link" placeholder="请输入紧急联系人1姓名" v-model="linkNaem1" v-validtype="rules.linkNaem1" :state="rules.linkNaem1.state"></mt-field>
					<mt-field label="*紧急联系人1关系" class="link" placeholder="请输入紧急联系人1关系" v-model="relation1" v-validtype="rules.relation1" :state="rules.relation1.state"></mt-field>
					<mt-field label="*紧急联系人1电话" class="link" placeholder="请输入紧急联系人1电话" v-model="mobileTel1" v-validtype="rules.mobileTel1" :state="rules.mobileTel1.state"></mt-field>
					<!-- <mt-field label="紧急联系人2姓名" class="link" placeholder="请输入紧急联系人2姓名" v-model="linkNaem2" ></mt-field>
					<mt-field label="紧急联系人2关系" class="link" placeholder="请输入紧急联系人2关系" v-model="relation2" ></mt-field>
					<mt-field label="紧急联系人2电话" class="link" placeholder="请输入紧急联系人2电话" v-model="mobileTel2" ></mt-field> -->
					<!-- <mt-button  type="primary" size="large"  @click="">上一步</mt-button> -->
					<mt-button  type="primary" size="large"  @click="showFlow(3)">下一步</mt-button>
				</div>
				<div v-show="flowId==3">
					<mt-header :title="title" class="cust-header1" style="background:#ef4f4f" ><mt-button icon="back" @click="showFlow(2)" slot="left">返回</mt-button></mt-header>
					<mt-field label="*交易密码" placeholder="请输入交易密码" v-model="fundPassword" type="password" v-validtype="rules.fundPassword" :state="rules.fundPassword.state"></mt-field>
					<mt-field label="*确认密码" placeholder="请再次输入交易密码" v-model="fundPasswordCom" type="password" v-validtype="rules.fundPassword" :state="rules.fundPassword.state"></mt-field>
					<!-- <mt-button  type="primary" size="large"  @click="">上一步</mt-button> -->
					<mt-button  type="primary" size="large"  @click="showFlow(4)">下一步</mt-button>
				</div>
				<div v-show="flowId==4">
					<mt-header :title="title" class="cust-header1" style="background:#ef4f4f" ><mt-button icon="back" @click="showFlow(3)" slot="left">返回</mt-button></mt-header>
					<mt-field label="银行代码" placeholder="请输入银行代码" v-model="bank" v-lfoucs="showBank"></mt-field>

					<mt-field label="银行支行 " placeholder="请输入银行支行 " v-model="bankbranch" ></mt-field>
					<mt-field label="银行网点  " placeholder="请输入银行网点 " v-model="bankNet" ></mt-field>
					<mt-field label="银行账户" placeholder="请输入银行账户" v-model="bankAcct" ></mt-field>
					<!-- <mt-button  type="primary" size="large"  @click="">上一步</mt-button> -->
					<mt-button  type="primary" size="large"  @click="realNameAsk">提交</mt-button>

				</div>


				<mt-popup
		        v-model="bankVisible"
		        style="width:100%"
		        position="bottom"
		      >
		        <button class="btn" @click="dissBank">取消</button>
		        <button class="btn" @click="sureBank">确认</button>
		        <mt-picker :slots="bankSlots" class="acctTypepicker" valueKey="name" @change="bankChange" :visible-item-count="3"></mt-picker>
		      </mt-popup>


				<mt-popup
							v-model="selectTypeVisibleAcct"
							style="width:80%"
							pop-transtion="popop-fade"
							class="select-dict"
						>
							<mt-picker :slots="selectTypeSlotsAcct"  valueKey="name" @change="selectTypeChangeAcct" :visible-item-count="3"></mt-picker>
							<div class="select-dict-btns"><button class="btn" @click="dissSelectTypeAcct">取消</button>
								<button class="btn" @click="sureSelectTypeAcct">确认</button>
								</div>
						</mt-popup>



			<mt-popup
	          v-model="selectTypeVisible"
	          style="width:80%"
	          pop-transtion="popop-fade"
	          class="select-dict"
	        >
	          <mt-picker :slots="selectTypeSlots"  valueKey="name" @change="selectTypeChange" :visible-item-count="3"></mt-picker>
	       		<div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
	          	<button class="btn" @click="sureSelectType">确认</button>
	          	</div>
        	</mt-popup>
        	<mt-popup
	          v-model="selectTypeVisible2"
	          style="width:80%"
	          pop-transtion="popop-fade"
	          class="select-dict"
	        >
	          <mt-picker :slots="selectTypeSlots2"  valueKey="name" @change="selectTypeChange2" :visible-item-count="10"></mt-picker>
	       		<div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
	          	<button class="btn" @click="sureSelectType">确认</button>
	          	</div>
        	</mt-popup>

        	<mt-popup
	          v-model="selectTypeVisible3"
	          style="width:80%"
	          pop-transtion="popop-fade"
	          class="select-dict"
	        >
	          <mt-picker :slots="selectTypeSlots3"  valueKey="name" @change="selectTypeChange3" :visible-item-count="7"></mt-picker>
	       		<div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
	          	<button class="btn" @click="sureSelectType">确认</button>
	          	</div>
        	</mt-popup>

        	<mt-popup
	          v-model="selectTypeVisible4"
	          style="width:80%"
	          pop-transtion="popop-fade"
	          class="select-dict"
	        >
	          <mt-picker :slots="selectTypeSlots4"  valueKey="name" @change="selectTypeChange4" :visible-item-count="7"></mt-picker>
	       		<div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
	          	<button class="btn" @click="sureSelectType">确认</button>
	          	</div>
        	</mt-popup>

        	<mt-popup
	          v-model="selectTypeVisible5"
	          style="width:80%"
	          pop-transtion="popop-fade"
	          class="select-dict"
	        >
	          <mt-picker :slots="selectTypeSlots5"  valueKey="name" @change="selectTypeChange5" :visible-item-count="7"></mt-picker>
	       		<div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
	          	<button class="btn" @click="sureSelectType">确认</button>
	          	</div>
        	</mt-popup>
        	<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
        </div>
	</div>
</template>
<script>
import {Field,Picker,Popup,MessageBox,Actionsheet,Header} from 'mint-ui'
import custHeader from  'components/custHeader'
import zjz from  'assets/2276.png'

Vue.component(Actionsheet.name,Actionsheet);
Vue.component(Field.name,Field)
Vue.component(Popup.name,Popup)
Vue.component(Picker.name,Picker)
Vue.component(Header.name,Header);
export default{
	data(){
		return {
			picMsg:"",
			rules:{
				investorSname:{str:"required",state:""},
				idCode:{str:"required",state:""},
				// idName:{str:"required",state:""},
				certValidDate:{str:"required",state:""},
				idIssAgcy:{str:"required",state:""},
				postcode:{str:"required",state:""},
				address:{str:"required",state:""},
				linkNaem1:{str:"required",state:""},
				relation1:{str:"required",state:""},
				mobileTel1:{str:"required",state:""},
				fundPassword:{str:"required",state:""}
			},
			dicts:{
				idType:{
 				 name:'ID_TYPE',
 				 dataKey:"selectTypeSlotsAcct"
 			 },
				currency:{
					name:"CURRENCY",
					dataKey:"selectTypeSlots"
				},
				countryCode:{
					name:"COUNTRY_CODE:&156",
					dataKey:"selectTypeSlots2"
				}
			},
		areas:{
			province:{
				name:"province",
				dataKey:"selectTypeSlots3"
			},
			city:{
				name:"city",
				dataKey:"selectTypeSlots4"
			},
			area:{
				name:"area",
				dataKey:"selectTypeSlots5"
			}
		},
			actions:[{name:"拍照",
						method:()=>{
							this.selectPic();
						}
						},
						{name:"从相册选择",
						method:()=>{
							this.selectPic();
						}
						}
			],
			sheetVisible:false,
			zjzshow:false,
			zjz:zjz,
		flowId:"1",
		title:"实名认证",

		idType:{name:"",value:""},
		investorSname:'',
		// idName:"",
		idCode:"",
		certValidDate:"",
		idIssAgcy:"",

		nationality:{name:"",value:""},
		province:{name:"",value:""},
		city:{name:"",value:""},
		area:{name:"",value:""},
		postcode:"",
		address:"",
		linkNaem1:"",
		linkNaem2:"",
		relation1:"",
		relation2:"",
		mobileTel1:"",
		mobileTel2:"",

		bank:'',
		bankCode:"",
		bankbranch:"",
		bankNet:"",
		bankAcct:"",
		fundPassword:"",
		fundPasswordCom:'',


		bankVisible:false,
		bankSlots:[{
			flex:2,
			// values:[{value:'0',name:'中国工商银行'},{value:'1',name:'杭州银行'},{value:'2',name:'浙商银行'}],
			values:[],
			className:"slots"
		}],

		selectTypeVisibleAcct:false,
		 selectTypeSlotsAcct:[{
			flex:1,
			values:[],
			className:"slots"
		}],

		selectTypeVisible:false,
		selectTypeSlots:[{
        flex:1,
        values:[{value:'0',name:'人民币2'},{value:'1',name:'美元2'},{value:'2',name:'港币2'}],
        className:"slots"
      }],
      selectTypeVisible2:false,
		selectTypeSlots2:[{
        flex:1,
        values:[],
		}],
		selectTypeVisible3:false,
		selectTypeSlots3:[{
        flex:1,
        values:[],
		}],
		selectTypeVisible4:false,
		selectTypeSlots4:[{
        flex:1,
        values:[],
		}],

		selectTypeVisible5:false,
		selectTypeSlots5:[{
        flex:1,
        values:[],
		}]

		}
	},
	created:function(){
		// this.qryCustBaseInfo();
		var self = this;
    axiosUtil.upCltBankCodeQry().then(function(data){
      var data = data.data.data;
      for (var i = 0; i < data.length; i++) {
        self.bankSlots[0].values.push({value:data[i].orgCode,name:data[i].orgFullName})
      }
      this.bankCode = this.bankSlots[0].values[0].value
    })
	},
	 components:{
    custHeader
  },
	methods:{
		showBank:function(){
      this.bankVisible = true
    },
    bankChange:function(picker,value){
    //  console.log(value)
     if(value[0]) {
       this.bank = value[0].name
       this.bankCode =value[0].value
     }

    // this.acctTypeValue = value[0].value
   },
   dissBank:function(){
     this.bankVisible = false
   },
   sureBank:function(){
     this.bankVisible = false
   },
		selectTypeChangeAcct:function(picker,value){
      console.log(value);
      if(value[0]){
        this.idType.name=value[0].name;
        this.idType.value=value[0].value;
      }
    },
    // selectTypeVisibleShow:function(){
    //   this.selectTypeVisibleAcct=true;
    // },
    sureSelectTypeAcct:function(){
      this.selectTypeVisibleAcct = false
    },
    dissSelectTypeAcct:function(){
      this.selectTypeVisibleAcct = false
    },


		 qryCustBaseInfo:function(){
        axiosUtil.qryCustBaseInfo().then(data=>{
          var data=data.data;
          console.log(data);
        // data= JSON.parse(data.data)
        this.idType.value=data.data.idType;
        if(data.errorcode==0){
        	window.dict("ACCT_TYPE",data.data.idType).then(data=>{
        		this.idType.name=data

        	});
        }else{
        	MessageBox("提示",data.errormsg)
        }

        })
      },
		upload:function(e){

			let files=e.target.files || e.dataTransfer.files;
			if(!files.length ) return;
			var pars=new FormData();
			pars.append("file",files[0],files[0].name);
			pars.append("uploadUser","279947");
			pars.append("docBizId","4001");
			pars.append("docBizSubid","1");
			axiosUtil.realNamePicUpload(pars).then(data=>{
			})


		},
		showSheet:function(){
			this.sheetVisible=true;
		},
		selectPic:function(){
			this.zjzshow=true;

		},
		selectTypeChange:function(picker,value){
			console.log(value);
			if(value[0]){
				//this.idType=value[0].name;
			}
		},
		selectTypeChange2:function(picker,value){
			console.log(value);
			if(value[0]){
				this.nationality.name=value[0].name;
				this.nationality.value=value[0].value;
			}
		},
		selectTypeChange3:function(picker,value){
			console.log(value);
			if(value[0]){
				this.province.name=value[0].name;
				this.province.value=value[0].value;
				window.areaCity("city",value[0].value).then(data=>{
					var items=[];
					 data.forEach(item=>{
                      items.push({value:item.ddItem,name:item.ddItemName});
                    })
					 this.selectTypeSlots4=[{
				        flex:1,
				        values:items,
						}];
				})
			}
		},
		selectTypeChange4:function(picker,value){
			console.log(value);
			if(value[0]){
				this.city.name=value[0].name;
				this.city.value=value[0].value;
				window.areaCity("area",value[0].value).then(data=>{
					console.log(data);
					var items=[];
					 data.forEach(item=>{
                      items.push({value:item.ddItem,name:item.ddItemName});
                    })
					 this.selectTypeSlots5=[{
				        flex:1,
				        values:items,
						}];
				})
			}
		},
		selectTypeChange5:function(picker,value){
			console.log(value);
			if(value[0]){
				this.area.name=value[0].name;
				this.area.value=value[0].value;
			}
		},
		selectTypeVisibleShowAcct:function(){
			this.selectTypeVisibleAcct=true;
		},
		selectTypeVisibleShow:function(){
			this.selectTypeVisible=true;
		},
		selectTypeVisibleShow2:function(){
			this.selectTypeVisible2=true;
		},
		selectTypeVisibleShow3:function(){
			this.selectTypeVisible3=true;
		},
		selectTypeVisibleShow4:function(){
			this.selectTypeVisible4=true;
		},
		selectTypeVisibleShow5:function(){
			this.selectTypeVisible5=true;
		},
		dissSelectType:function(){
			this.currency='';
			this.selectTypeVisible=false;
			this.selectTypeVisible2=false;
			this.selectTypeVisible3=false;
			this.selectTypeVisible4=false;
			this.selectTypeVisible5=false;
		},
		sureSelectType:function(){
			if(!this.currency){
		        this.currency = this.selectTypeSlots[0].values[0].name;
		      }
		      this.selectTypeVisible=false;
			this.selectTypeVisible2=false;
			this.selectTypeVisible3=false;
			this.selectTypeVisible4=false;
			this.selectTypeVisible5=false;
		},
		showFlow:function(index){
				if(index == 4){
					if(this.fundPassword != this.fundPasswordCom){
						MessageBox("两次密码填写不一样")
						return false
					}
				}
				this.flowId=index;
		},
		realNameAsk:function(){


			var rulesKey = Object.keys(this.rules)
			for (var i = 0; i < rulesKey.length; i++) {
				if(this.rules[rulesKey[i]].state !== 'success'){
					MessageBox("提示","请将必填项填写完整")
					return false
				}
			}
				 if(global.isOpenMd5){
	        this.fundPassword=global.md5(this.fundPassword);
	      }
			var pars={
		idType:this.idType.value,
		investorSname:this.investorSname,
		// idName:this.idName,
		idCode:this.idCode,
		certValidDate:this.certValidDate,
		idIssAgcy:this.idIssAgcy,

		nationality:this.nationality.value,
		province:this.province.value,
		city:this.city.value,
		county:this.area.value,
		postcode:this.postcode,
		address:this.address,
		linkNaem1:this.linkNaem1,
		linkNaem2:this.linkNaem2,
		relation1:this.relation1,
		relation2:this.relation2,
		mobileTel:this.mobileTel,
		mobileTel2:this.mobileTel2,

		bankCode:this.bankCode,
		bankbranch:this.bankbranch,
		bankNet:this.bankNet,
		bankAcct:this.bankAcct,
		fundPassword:this.fundPassword,


			}
			var self = this;
			axiosUtil.realNameAsk(pars).then(data=>{
				var data=data.data;
				if(data.errorcode==1){
					 MessageBox("提示", data.errormsg);
				}else{

					// if(self.bankCode && self.bankAcct){
					// 	MessageBox("提示", "实名认证信息提交成功！请先去绑定银行卡！");
					// 	router.push("/personal")
					// }else{
					// 	MessageBox("提示", "实名认证信息提交成功！请先去绑定银行卡！");
					// 	router.push("/personal")
					// }
					MessageBox("提示", "业务已受理,请转入资金,完成实名认证");
					router.push("/personal")

				}

			})
		}
	}
}

</script>
<style scoped>
.fund-manage-con{

}
.fund-manage-contain{
	padding: 12px;
	overflow-y:auto;
	    height: 100%;
    padding-bottom: 48px;
}
.select-dict{
	font-size: 14px;
}
.cust-header1{
	width: 100%;
	position: fixed;
	top: 0;
}
	.select-dict button{
		    width: 50%;
    float: left;
    height: 28px;
    border: none;
    background: #fff;
    border-top: 1px solid #ccc;
	}
	.select-dict button:last-child{
		border-left: 1px solid #ccc;
	}
	.select-dict-btns{
		position: absolute;
		bottom: 0;
		bottom: -28px;
    width: 100%;
	}
	.zjz{
		width: 100%;
	}
	.btn{
	  width: 49%;
	  background-color: #fff;
	  border: 0;
	  height: 40px;
	  font-size: 20px;
	  outline: 0;
	}
</style>



